@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
.home__page {
    height: 87vh;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
    margin: 0 auto;
    text-align: center;
  }
  
  .login__message {
    display: flex;
    flex-direction: column;
  }
  
  .login__message > h2 {
    font-size: 80px;
    margin: 0;
  }
  
  .login__message > h1 {
    font-size: 100px;
    width: 80%;
    line-height: 1em;
    margin: 10px auto;
    margin-top: 0;
    font-weight: 900;
    letter-spacing: -4px;
    color: rgb(17, 17, 17);
    text-decoration: underline;
  }
  
  .login__message > p {
    padding: 15px 0;
    line-height: 1.5em;
    width: 50%;
    margin: auto;
    font-size: 16px;
  }
  
  .login__button {
    width: 300px;
    margin: 15px auto 0px auto;
    padding: 15px 0;
    border: none;
    background: rgb(17, 17, 17);
    font-family: "Inter";
    color: white;
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
  }  
.navbar {
    display: flex;
    justify-content: space-between;
    height: 13vh;
    align-items: center;
    padding: 0px 80px;
    background: white;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    background: #1b1b1d;
    color: white;
  }
  
  .navbar__header {
    font-size: 20px;
    font-weight: 700;
  }
  
  .navbar__user__data {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 30%;
  }
  
  .user {
    border: 2px solid rgb(0, 255, 234);
  }
  
  .signedIn {
    font-size: 15px;
  }
  
  .logout__button {
    padding: 12px 25px;
    border-radius: 50px;
    border: none;
    background: rgb(39, 39, 39);
    font-family: "Inter";
    color: white;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
  }
  
  .notSignedIn {
    font-size: 15px;
  }
  
  @media screen and (max-width: 800px) {
    .navbar {
      flex-direction: column;
      width: 30%;
      height: auto;
      justify-content: start;
      padding: 0 10%;
    }

    .navbar__header {
      margin-top: 50px;
      font-size: 16px;
    }

    .navbar__user__data {
      flex-direction: column;
      justify-content: space-evenly;
      margin-top: 50px;
      height: 5%;
      font-size: 12px;
    }

    .signedIn {
      font-size: 12px;
      padding: 0;
    }

    .logout__button {
      margin-left: 0;
      font-size: 12px;
    }

    .notSignedIn {
      font-size: 12px;
    }
  }
  
