.blog__search {
    flex: 1 1;
    width: 60%;
    display: flex;
    padding: 10px 0px;
    margin: 20px auto;
    justify-content: space-between;
    background: rgb(39, 39, 39);
    border-radius: 5px;
  }
  
  .search {
    width: 80%;
    padding: 5px 30px;
    padding-left: 30px;
    background: none;
    font-family: "Inter";
    border: none;
    outline: none;
    font-size: 15px;
    color: white;
  }
  
  .submit {
    width: 18%;
    font-family: "Inter";
    background: none;
    color: white;
    font-size: 14px;
    border: none;
    cursor: pointer;
    margin-right: 10px;
    padding: 12px 0px;
    border-radius: 5px;
    transition: 0.1s ease-in-out;
    background: rgb(31, 31, 31);
    outline: none;
  }
.blog__page {
    padding: 30px 50px;
  }
  
  .blog__page__header {
    letter-spacing: -1px;
    font-size: 35px;
  }
  
  .blogs {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: auto;
    grid-gap: 50px;
    padding: 40px 0;
  }
  
  .blog {
    min-width: 300px;
    margin: auto;
    background: white;
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);
    height: 100%;
    text-decoration: none;
    color: black;
  }
  
  .blog > div {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    padding: 0px 30px 0px 30px;
  }
  
  .blog > img {
    width: 100%;
  }
  
  .sourceName > span {
    display: inline;
    background: rgba(38, 0, 128, 0.226);
    font-size: 11px;
    padding: 5px 6px;
    color: rgba(0, 11, 43, 0.534);
    border-radius: 3px;
  }
  
  .sourceName > p {
    font-size: 10px;
    font-weight: 500;
    padding-left: 10px;
  }
  
  .sourceName {
    display: flex;
    align-items: center;
  }
  
  .blog > div > h1 {
    font-size: 25px;
    letter-spacing: -0.6px;
    line-height: 1.3em;
    padding: 10px 0;
    font-weight: 700;
    text-decoration: underline;
  }
  
  .blog > div > p {
    font-size: 13px;
    line-height: 1.6em;
    color: rgb(90, 90, 90);
    padding-bottom: 30px;
  }
  
  .no__blogs {
    font-size: 25px;
    font-weight: 500;
    line-height: 1.5em;
  }

  @media screen and (max-width: 800px) {
    .blog__page {
      padding: 30px 50px;
    }

    .blogs {
      grid-template-columns: auto;
    }
  }
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Inter", sans-serif;
}

.app {
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 800px) {
    .app {
        flex-direction: row;
    }
}
